.navbar-root {
    line-height: 0px;
    border: #000000 solid;
    border-top-width: 2px;
    border-right-width: 0;
    border-left-width: 0;
    border-bottom-width: 2px;
}

.navbar {
    background-color: #1aa3ff;
}

.navbar-name{
    color: #000000;
    font: 30px normal;
    /*font-family: 'Knewave', cursive;*/
    font-family: 'Londrina Solid', cursive;
}

/*
Page Links
*/
.navbar-root .navbar .nav-link {
    color: #000000;
    text-outline: #000000 30px;
    font-family: "Segoe UI", "Roboto", "Oxygen", -apple-system, BlinkMacSystemFont, "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
}

.navbar-root .navbar .nav-link:hover {
    color: #ffffff;
    /*opacity: 65%;*/
}

.navbar-root .navbar .nav-link:focus {
    /*color: #f2f2f2;*/
    /*color: #000000;*/
    color: #ffffff;
}

.navbar-root .navbar .nav-link:active {
    /*color: #f2f2f2;*/
    /*color: #000000;*/
    color: #ffffff;
}
